@import "../index.sass";

.mainSection.homeSection {
    display: block;
    padding-bottom: 70px;
    overflow: hidden;
    h1, h2 {
        width: 51%;
        text-align: center; }
    h1 {
        margin-top: 15%;
        font-size: 70px;
        opacity: 0;
        animation: letterAppear .5s linear forwards; }
    h2 {
        margin-top: 20px;
        font-size: 35px;
        margin-bottom: 100px;
        .letter {
            opacity: 0;
            animation: letterAppear .5s linear forwards;
            @keyframes letterAppear {
                100% {
                    opacity: 1; } } } }
    h3 {
        position: static;
        opacity: 0;
        font-size: 30px;
        animation: textAppear 1s linear forwards 2s; }
    p {
        padding: 70px 70px 0 70px;
        font-size: 24px;
        opacity: 0;
        animation: textAppear 1s linear forwards 2s; } }
@keyframes textAppear {
    100% {
        opacity: 1; } }

