@import "../index.sass";

// CURIOSITIES SECTION

.curiositiesSection {
    height: 100vh;
    @include flexCenter();
    .navCuriosities {
        position: relative;
        width: 60%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        margin-top: 30px;
        z-index: 1;
        .curiositiesNavBtn {
            position: relative;
            width: 47%;
            height: 40%;
            margin: 20px 0;
            border: 3px solid $white;
            text-decoration: none;
            font-size: 30px;
            transition: .5s;
            background-color: #000;
            overflow: hidden;
            h1 {
                position: absolute;
                top: 50%;
                height: 20%;
                transform: translateY(-50%);
                transition: .5s;
                color: $white; }
            .description {
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 80%;
                padding: 12px;
                @include flexCenter();
                color: $black;
                text-align: center;
                font-size: 20px;
                transform: translateY(100%);
                transition: .5s; } }
        .curiositiesNavBtn:nth-child(4):hover {
            background-color: $white;
            h1 {
                top: 0;
                transform: none;
                color: $black;
                letter-spacing: 1px; }
            .description {
                transform: none; } }

        .curiositiesNavBtn:hover {
            background-color: $white;
            h1 {
                color: $black;
                letter-spacing: 1px; } } } }

.backToCuriositiesBtn {
    position: fixed;
    padding: 5px 10px;
    z-index: 1;
    left: 10px;
    bottom: 100px;
    color: $white;
    background-color: $black;
    font-size: 25px;
    text-decoration: none;
    transition: .3s;
    animation: backToCuriositiesBtnMove 2s linear infinite;
    .fa-arrow-left {
        margin-right: 5px; } }
.backToCuriositiesBtn:hover {
    background-color: $white;
    color: $black; }
@keyframes backToCuriositiesBtnMove {
    33% {
        transform: translateX(10px); }
    66% {
        transform: translateX(-10px); }
    100% {
        transform: none; } }


// WORKSHOPS SECTION

.mainSection.workshopsSection {
    height: 100vh;
    .workshopsImgContainer {
        width: 70%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        .img {
            width: 15%;
            height: 20%;
            margin: 20px 0;
            @include flexCenter();
            border: 1px solid white;
            img {
                height: 100%; } } } }

// EXPLORATION SECTION



.mainSection.explorationSection {
    height: 100vh;
    .ExplorationImgContainer {
        width: 60%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        .img {
            width: 19%;
            height: 17%;
            margin: 20px 0;
            @include flexCenter();
            border: 1px solid white;
            img {
                height: 100%; } } } }

// COMICS-DUMMY SECTION

.mainSection.comicsDummy {
    height: 100vh;
    .comicsDummyImgContainer {
        width: 70%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        .img {
            width: 18%;
            height: 17%;
            margin: 20px 0;
            @include flexCenter();
            border: 1px solid white;
            img {
                height: 100%; } }
        .img:nth-child(1), .img:nth-child(2), .img:nth-child(3), .img:nth-child(4) {
            width: 23%; } } }


// COMICS-ILLUSTRATION SECTION

.mainSection.comicsIllustrationSection {
    height: 100vh;
    position: relative;
    .comicsIllustrationImgContainer {
        @include flexCenter();
        width: 90%;
        height: 60%;
        justify-content: space-around;
        .img {
            height: 20%;
            width: 17%;
            margin: 10px 0;
            @include flexCenter();
            border: 1px solid white;
            img {
                height: 100%; } } } }

